<template>
  <div id="encyclopedias">


    <new-layout>
    <template slot="top">



      <div id="announce_header">
        <i class="iconfont icon-fanhui" @click="$router.push('encyclopedias')"></i>
        <span class="tit text-overflow">
      <slot>指标详情</slot>
    </span>
        <i style="opacity: 0;" class="iconfont icon-fanhui"></i>
      </div>





    </template>


      <template slot="center">

        <header>{{res.result.knowledge.dn}}</header>

        <table class="fz24">
          <tr>
            <td class="width100 fz24">疾病名称:</td>
            <td>{{res.result.knowledge.dn}}</td>
          </tr>
          <tr>
            <td class="width100 fz24">所属科室:</td>
            <td>{{res.result.knowledge.ad}}</td>
          </tr>
          <tr>
            <td class="width100 fz24" v-html="disease"></td>
            <td>{{res.result.knowledge.dm}}</td>
          </tr>
          <tr>
            <td class="width100 fz24" v-html="classification"></td>
            <td>{{res.result.knowledge.dc}}</td>
          </tr>
        </table>
        <div class="disease_detail">
          疾病详情
        </div>
        <van-collapse v-model="activeNames">

          <van-collapse-item :title="reason" name="1" v-if="dd_cArrayShow">
            <li v-for="(item,index) in dd_cArray" class="sugges_li">
              <p class="title">{{item[0]}}</p>
              <p>{{item[1]}}</p>
            </li>
          </van-collapse-item>

          <van-collapse-item title="症状" name="2" v-if="dd_sArrayShow">
            <li v-for="(item,index) in dd_sArray" class="sugges_li">
              <p class="title">{{item[0]}}</p>
              <p>{{item[1]}}</p>
            </li>
          </van-collapse-item>

          <van-collapse-item title="风险或并发症" name="3"  v-if="dd_rArrayShow">
            <li v-for="(item,index) in dd_rArray" class="sugges_li">
              <p class="title">{{item[0]}}</p>
              <p>{{item[1]}}</p>
            </li>
          </van-collapse-item>






          <van-collapse-item title="专家建议" name="4"  v-if="dd_lArrayShow">
             <van-collapse-item title="生活方式" name="5">
              <li v-for="(item,index) in dd_lArray" class="sugges_li">
                <p class="title">{{item[0]}}</p>
                <p>{{item[1]}}</p>
              </li>
            </van-collapse-item>





            <van-collapse-item title="饮食建议" name="6">
              <span class="suit">宜</span>
              <li v-for="(item,index) in dd_dArray" class="sugges_li"   v-if="dd_dArrayShow">
                <p class="title">{{item[0]}}</p>
                <p>{{item[1]}}</p>
              </li>
              <span class="avoid">忌</span>
              <li v-for="(item,index) in dd_dArray1" class="sugges_li" v-if="dd_dArray1Show">
                <p class="title">{{item[0]}}</p>
                <p>{{item[1]}}</p>
              </li>
            </van-collapse-item>



            <van-collapse-item title="医疗保健" name="8" v-if="dd_mArrayShow">
              <li v-for="(item,index) in dd_mArray" class="sugges_li">
                <p class="title">{{item[0]}}</p>
                <p>{{item[1]}}</p>
              </li>
            </van-collapse-item>


      </van-collapse-item>





          <van-collapse-item title="运动建议" name="9" v-if="dd_saArrayShow">
            <li v-for="(item,index) in dd_saArray" class="sugges_li">
              <p class="title">{{item[0]}}</p>
              <p>{{item[1]}}</p>
            </li>
          </van-collapse-item>
           <van-collapse-item title="生活常识" name="10" v-if="dd_nArrayShow">
            <li v-for="(item,index) in dd_nArray" class="sugges_li">
              <p class="title">{{item[0]}}</p>
              <p>{{item[1]}}</p>
            </li>
          </van-collapse-item>
           <van-collapse-item title="就医复查指南" name="11" v-if="dd_gArrayShow">
            <li v-for="(item,index) in dd_gArray" class="sugges_li">
              <p class="title">{{item[0]}}</p>
              <p>{{item[1]}}</p>
            </li>
          </van-collapse-item>

        </van-collapse>

      </template>
    </new-layout>

  </div>
</template>

<script>


  export default {
    data(){
      return {
        activeNames:['0'],
        res:'',
        dd_lArray:[],
        dd_dArray:[],
        dd_saArray:[],
        dd_mArray:[],
        dd_nArray:[],
        dd_cArray:[],//病因
        dd_sArray:[],
        dd_rArray:[],
        dd_gArray:[],
        dd_dArray1:[],
        disease:':',
        classification:'',
        reason:'',
        dd_cArrayShow:false,
        dd_sArrayShow:false,
        dd_rArrayShow:false,
        dd_lArrayShow:false,
        dd_dArrayShow:false,
        dd_dArray1Show:false,
        dd_mArrayShow:false,
        dd_saArrayShow:false,
        dd_nArrayShow:false,
        dd_gArrayShow:false,
      }
    },

    methods:{
      async init(){
        let _this=this;
        let key=this.$route.query.code;
        this.res= await this.$http.get(this.$HOST+'/open/xk/symptom?code='+key);
        if(this.res.result.type == 'index') {
            this.disease = "检查目的";
            this.classification = "判定标准";
            this.reason = "可能原因";
        } else {
          this.disease = "疾病概述";
          this.classification = "疾病分类";
          this.reason = "病因";
        }



    if(this.res.result.knowledge.dd_l) {
      this.dd_cArrayShow=true;
      this.common(this.res.result.knowledge.dd_l,_this.dd_lArray)
    }

        if(this.res.result.knowledge.dd_s) {
          this.dd_sArrayShow=true;
          this.common(this.res.result.knowledge.dd_s,_this.dd_sArray)
        }





     /*

          dd_dArrayShow:false,
          dd_mArrayShow:false,
          dd_saArrayShow:false,
          dd_nArrayShow:false,
          dd_gArrayShow:false,*/



        if(this.res.result.knowledge.dd_l) {
          this.dd_lArrayShow=true;
          this.common(this.res.result.knowledge.dd_l,_this.dd_lArray)
        }


        if(this.res.result.knowledge.dd_d_s) {
          this.dd_dArrayShow=true;
          this.common(this.res.result.knowledge.dd_d_s,_this.dd_dArray)
        }




        if(this.res.result.knowledge.dd_d_a) {
          this.dd_dArray1Show=true;
          this.common(this.res.result.knowledge.dd_d_a,_this.dd_dArray1)
        }


        if(this.res.result.knowledge.dd_sa) {
          this.dd_saArrayShow=true;
          this.common(this.res.result.knowledge.dd_sa,_this.dd_saArray)
        }


        if(this.res.result.knowledge.dd_m) {
          this.dd_mArrayShow=true;
          this.common(this.res.result.knowledge.dd_m,_this.dd_mArray)
        }



        if(this.res.result.knowledge.dd_n) {
          this.dd_nArrayShow=true;
          this.common(this.res.result.knowledge.dd_n,_this.dd_nArray)
        }



        if(this.res.result.knowledge.dd_c) {
          this.dd_cArrayShow=true;
          this.common(this.res.result.knowledge.dd_c,_this.dd_cArray)
        }




        if(this.res.result.knowledge.dd_r) {
          this.dd_rArrayShow=true;
          this.common(this.res.result.knowledge.dd_r,_this.dd_rArray)
        }



        if(this.res.result.knowledge.dd_g) {
          this.dd_gArrayShow=true;
          this.common(this.res.result.knowledge.dd_g,_this.dd_gArray)
        }










    },
   common(data,arrayname) {
       let resLen=data.split("@#");
        for(var i=0; i<resLen.length; i++){
          if(resLen[i]){
            arrayname.push(resLen[i].split("#@"));
          }
        }
      }
  },
    created(){
      this.init();
    }
  }
</script>

<style scoped lang="scss">
  $height80: 80px;
  $bg-fff: #ffffff;
  #encyclopedias {
    background: #f3f3f3;
    .sugges_li {
      border-bottom: 1px dashed #999999;
      margin-bottom: 12px;
      padding: 10px;
    }
    .title {
      color: orange;
      font-size: 24px;
    }
    p:nth-child(2){
      font-size: 18px;
    }
  }

  table{
    width: 96%;
    margin: auto;
    tr{
      height: 50px;
      line-height: 50px;
      td{
        font-size: 18px;
      }
      td:nth-child(2){
        font-weight: bold;
        color: #333333;
      }
    }
  }
  .van-cell__title span{
    font-size: 18px!important;
  }
  .width100{
    width: 150px;
    vertical-align: -webkit-baseline-middle;
  }

  header{
    font-size: 30px;
    background: #ffffff;
    text-indent: 18px;
    height: 80px;
    line-height: 80px;
    font-weight: bold;
  }
  .disease_detail{
     color: #089ec3;
    font-size: 15px;
    background: #ffffff;
    text-indent: 18px;
    height: 80px;
    line-height: 80px;
    font-weight: bold;
  }

  .suit{
    width: 30px;
    height: 30px;
    background: #68ba8a;
    display: inline-block;
    text-align: center;
    line-height: 30px;
    color: #ffffff;
    font-size: 14px;
  }
  .avoid{
    width: 30px;
    height: 30px;
    background: #d95d5d;
    display: inline-block;
    text-align: center;
    line-height: 30px;
    color: #ffffff;
    font-size: 14px;
  }

  .fz24{
    font-size: 24px;
  }



  #encyclopedias p{
    font-size: 28px!important;
  }

  #announce_header{
    width: 100%;
    height: 120px;
    color: #FFF;

    text-align: center;
    background: #6EAFFF;
    display: flex;
    align-items: center;
    justify-content: center;

    .iconfont{
      flex: .15;
      font-size: 50px;
      display: flex;
      justify-content: center;
    }

    span{
      flex: 1;
      font-size: 35px;
    }
  }
</style>



